import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { User } from '../models/user';
import { environment }from '../../environments/environment';
import {map} from 'rxjs/operators'; 


@Injectable({
  providedIn: 'root'
})
export class UserService {

  	urlApi:string="https://api-movil.herokuapp.com/api/user";
    urlLocal = "http://localHost:3000/api/user";
    url= this.urlApi;
    users: User;
    httpOptions = {
      headers: new HttpHeaders({'authorization': `Bearer ${environment.TOKEN}` ,
        'Content-Type':  'application/json','Accept':'application/json'})
    };

  constructor(public http: HttpClient) {
  }

  getUsers(){
  	return	new Promise(resolve =>{
      this.http.get(`${this.url}`, this.httpOptions)
           .subscribe(res=>{
             this.users = res as User;
             resolve(this.users);
            })
    })
  }

  postUser(user: User){
    //let token =this.auth.getToken();
   // let headerT = new Headers({'Authorization':`Bearer ${token.token}`,'Content-Type':'application/json'});
    return new Promise(resolve =>{
        this.http.post(this.url,user,this.httpOptions).subscribe(res =>{
          console.log(`guardado ${res}`)
          resolve(res);
         });
    })
         
  }

  putUser(user: User){
      return new Promise(resolve =>{
        this.http.put(`${this.url}/`,user).pipe(map(res =>{
        }));
      })
  }

  deleteUser(id: string){
   // let token =this.auth.getToken();
    //const httpOptions = {
      //headers: new HttpHeaders({'Authorization': `Bearer ${token.token}`,'Content-Type':  'application/json','Accept':'application/json'})};
    return new Promise(resolve =>{
      this.http.delete(`${this.url}/${id}`, this.httpOptions)
         .subscribe((res =>{
           console.log("eliminado")
           console.log(res);
           resolve(res)
         }));
    })
  }

}
